/* إعدادات الحواف والألوان العامة */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* تنسيق القسم الرئيسي */
  .header {
    background-color: #2c3e50; /* لون خلفية غامق */
    padding: 40px 0;
    color: white;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .header-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  
  /* تنسيق الشعار */
  .logo img {
    width: 120px;
    height: auto;
    margin-bottom: 20px;
    border-radius: 10px; /* جعل الشعار ذو زوايا مدورة */
  }
  
  /* تنسيق العنوان */
  h1 {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 10px;
    font-family: 'Arial', sans-serif;
    letter-spacing: 2px;
    text-transform: uppercase;
  }
  
  /* تنسيق اسم المعهد */
  .institute-name {
    font-size: 20px;
    font-weight: lighter;
    font-family: 'Arial', sans-serif;
    color: #ecf0f1; /* لون فاتح للكتابة */
    margin-top: 10px;
  }
  
  /* إضافة تأثيرات خاصة عند التحويم على العنوان */
  h1:hover {
    color: #f39c12; /* تغيير اللون عند التحويم */
    transition: color 0.3s ease;
  }